$search-padding: 12px;
$search-grid-padding: 25px;
$search-text-color: #222;
$btn-green: rgb(76, 175, 80);

.search {
    background-color: rgb(197, 208, 194);
    background-image: url('../../images/bg.jpg');
    background-position: center;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
    height: 100vh;
    min-height: 240px;
    color: $search-text-color;

    .call-to-action {
        position: absolute;
        top: 18%;
        font-size: 1.2rem;
        font-weight: 200;
        width: 100%;
        padding: 0 20px;
        text-align: center;
    }

    .search-form {
        z-index: 9999;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        width: 100%;
        max-width: 550px;
        height: auto;
        min-height: 150px;
        padding: $search-grid-padding;
        background-color: white;
        -webkit-box-shadow: 0px 5px 10px -2px rgba(0, 0, 0, 0.5);
                box-shadow: 0px 5px 10px -2px rgba(0, 0, 0, 0.5);

        .search-label {
            padding: $search-padding;
            -ms-flex-item-align: start;
                align-self: flex-start;
        }

        .search-text {
            font-size: 1rem;
            padding: $search-padding;
            border: none;
            width: 100%;
            color: $search-text-color;
            background-color: rgba(0, 0, 0, 0);
            border-bottom: 1px solid #666;
            -webkit-transition: border-bottom-color 1s;
            -o-transition: border-bottom-color 1s;
            transition: border-bottom-color 1s;

            &:focus {
                outline: none;
                border-bottom-color: $btn-green;
            }
        }

        .search-btn-link-container {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row-reverse;
                flex-direction: row-reverse;
            -ms-flex-pack: justify;
                justify-content: space-between;
            width: 100%;
            margin-top: $search-grid-padding;

            .search-submit-button {
                margin: 0;
                padding: $search-padding;
                padding-left: 24px;
                padding-right: 24px;
                font-size: 1rem;
                border: none;
                border-radius: 50px;
                color: #EEE;
                font-weight: 200;
                background-color: $btn-green;
                cursor: pointer;

                &:disabled {
                    opacity: .7;
                }

                &:focus {
                    outline: none;
                }
            }

            .search-help-link {
                padding: $search-padding;
                color: $btn-green;
            }
        }
    }
}

@media screen and (max-width: 500px) {
    .search {
        .call-to-action {
            font-size: 1rem;
        }
    }
}


