$green: rgb(76, 175, 80);
$darker-green: rgb(43, 120, 43);

.main-header {
    -webkit-box-shadow: 0px 0px 15px -5px black;
            box-shadow: 0px 0px 15px -5px black;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    top: 0;
    padding: 20px;

    .main-header-logo-heading-wrapper {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-align: center;
            align-items: center;

        .main-header-logo {
            height: 35px;
        }

        .main-header-heading {
            color: #222;
            font-size: 1.5rem;
            font-weight: 200;
            margin: 0;
            margin-left: 10px;
            -ms-flex-item-align: center;
                align-self: center;
        }
    }

    .header-help-link {
        color: $darker-green;

        .link-text {
            display: inline;
        }

        .link-icon {
            display: none;
        }
    }
}

@media screen and (max-width: 460px) {
    .main-header {
        .main-header-logo-heading-wrapper {
            .main-header-heading {
                font-size: 1.3rem;
            }
        }

        .header-help-link {
            text-decoration: none;

            .link-text {
                display: none;
            }

            .link-icon {
                display: inline;
                font-size: 2rem;
                color: $green;
            }
        }
    }
}


